export function getIconId(fieldId: number) {
  for (const each of fieldIdToTranslationMap) {
    for (let i = 0; i < each.ids.length; i++) {
      const id = each.ids[i];
      if (id === fieldId) {
        return each.iconOffset + i;
      }
    }
  }
  throw new Error(`Could not find icon offset for ${fieldId}.`);
}
